@import url("https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  scroll-behavior: smooth;
  font-family: "Montserrat", sans-serif;
  font-feature-settings: "clig" off, "liga" off;
  font-style: normal;
}

body {
  background-color: #11181f;
  width: 100%;
  overflow-x: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 40px;
}

section {
  margin-top: 100px;
}

h2 {
  color: #c2d3dc;
  font-size: 32px;
  font-weight: 400;
  line-height: 125%;
}
h2 span {
  color: #c70000;
  font-family: "Architects Daughter", cursive;
}

h3 {
  color: #c2d3dc;
  font-size: 20px;
  font-weight: 200;
  line-height: 125%;
}
h3 p {
  font-size: 20px;
}

.social {
  display: flex;
  width: 60px;
  height: 60px;
  justify-content: center;
  align-items: center;
  border-radius: 100px;
  border: 2px solid #adb8c1;
  color: #c9d7e1;
  transition: 0.5s all;
  cursor: pointer;
}
@media (hover: hover) {
  .social:hover {
    background: #11181f;
    color: #c70000;
    border: 2px solid #c70000;
  }
}

.input {
  gap: 8px;
  height: 64px;
  width: 400px;
  border-radius: 5px;
  color: #c9d7e1;
  background: #11181f;
  padding: 20px;
  transition: all 0.5s ease-out;
  border: 1px solid #adb8c1;
}

.btn {
  display: flex;
  gap: 8px;
  justify-content: center;
  height: 64px;
  width: 200px;
  border-radius: 5px;
  color: #c9d7e1;
  text-decoration: none;
  background: #11181f;
  text-align: center;
  padding: 20px 78px;
  border: none;
  transition: all 0.5s ease-out;
}
.btn:hover {
  color: #c70000;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-top: 32px;
}
.form .btn {
  width: 400px;
  background: #11181f;
}

.header {
  height: 120px;
  margin: 0;
}
.header_logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.header h1 {
  margin: 0;
  margin-top: 10px;
  width: 150px;
  height: 100%;
  text-align: end;
  font-size: 42px;
  background-image: url("../assets/img/logo.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  cursor: pointer;
}
.header_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  min-height: 60px;
  align-items: center;
}
.header_logo-hamburger-wrapper {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_logo-hamburger-wrapper .hamburger {
  display: none;
}
.header_list {
  display: flex;
  list-style-type: none;
  padding: 0;
  column-gap: 40px;
}

.link-text {
  color: #c2d3dc;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  text-decoration: none;
  padding: 10px;
  border-bottom: 2px solid transparent;
  transition: 0.5s all;
}
@media (hover: hover) {
  .link-text:hover {
    color: #c70000;
    border-bottom: 2px solid #c70000;
  }
}

.enjoy {
  color: #c9d7e1;
  margin-top: 20px;
  padding: 100px;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}
.enjoy_video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  display: block;
  z-index: 0;
}
.enjoy .gradient-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom left, rgb(0, 2, 3), rgba(91, 110, 123, 0.456));
  z-index: 1;
  pointer-events: none;
}
.enjoy_hero {
  text-align: right;
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  align-self: stretch;
  display: flex;
  gap: 40px;
}
.enjoy_title {
  max-width: 530px;
  margin: 0;
  font-size: 52px;
  font-weight: 400;
  line-height: 105%;
}
.enjoy_title span {
  color: #c70000;
  font-family: "Architects Daughter", cursive;
}
.enjoy_descr {
  max-width: 530px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.about_title {
  font-size: 40px;
}

.gallery {
  margin-top: 40px;
  display: flex;
  gap: 40px;
  justify-content: center;
}
.gallery_column {
  display: flex;
  flex-direction: column;
  gap: 40px;
}
.gallery_img {
  filter: brightness(90%) contrast(90%);
  max-width: 660px;
  overflow: hidden;
  border-radius: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.gallery_img img {
  transform: scale(1.1);
  width: 100%;
  transition: transform 0.5s ease;
}
.gallery_img:hover img {
  transform: scale(1);
  object-fit: cover;
}
.gallery_img__square {
  aspect-ratio: 1/1;
}
.gallery_img__rectangle {
  aspect-ratio: 1.5/1;
}

.modal {
  z-index: 99;
  background: rgba(3, 12, 16, 0.895);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #c2d3dc;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.modal-closed {
  display: none;
}
.modal_window {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-height: 100vh;
}
.modal_wrapper {
  flex-shrink: 0;
  width: 100%;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal_img {
  display: block;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 4px;
}
.modal_content {
  max-width: 600px;
  display: flex;
  gap: 20px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.modal_title {
  display: none;
}
.modal_descr {
  text-align: center;
  background: transparent;
  margin-top: 12px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
}
.modal_close {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  background: transparent;
  color: #c70000;
  border: 1px solid transparent;
  border-radius: 5px;
  padding: 10px 20px;
  height: 44px;
  transition: all 0.5s ease;
}
.modal_close:hover {
  background: #11181f;
  color: #c9d7e1;
  cursor: pointer;
}

.projects {
  margin-top: 20px;
}
.projects_title {
  margin: 0 auto;
  max-width: 800px;
  text-align: center;
  color: #c2d3dc;
  font-size: 60px;
  font-weight: 600;
  line-height: 125%;
}
.projects_title span {
  color: #c70000;
  font-family: "Architects Daughter", cursive;
}

.tab {
  margin-top: 40px;
}
.tab_buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}
.tab_items {
  display: flex;
  justify-content: center;
}
.tab_content {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: center;
}
.tab_refresh {
  margin: 40px auto 0 auto;
}

.menu-card {
  width: 310px;
  border-radius: 4px;
  border: 1px solid #adb8c1;
  cursor: pointer;
  transition: all 0.5s;
}
.menu-card_img {
  width: 100%;
  height: 310px;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-card_img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  transform: scale(1);
  width: 100%;
  transition: all 0.5s;
}
.menu-card_img img:hover {
  transform: scale(1.2);
  object-fit: cover;
}
.menu-card_title {
  display: none;
}
.menu-card:hover img {
  transform: scale(1.2);
  object-fit: cover;
}
.menu-card_descr {
  display: none;
}

.tab-button {
  display: flex;
  padding: 16px 24px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 5px;
  border: 1px solid #adb8c1;
  cursor: pointer;
  transition: all 0.5s;
}
.tab-button_descr {
  color: #c2d3dc;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  background: transparent;
}
.tab-button:hover {
  background: rgba(47, 52, 57, 0.5098039216);
}
.tab-button:hover .tab-button_img {
  background: #11181f;
}
.tab-button.active {
  background: rgba(47, 52, 57, 0.5098039216);
}
.tab-button.active .tab-button_img {
  background: #11181f;
}
.tab-button.active .tab-button_descr {
  color: #c9d7e1;
}

.footer {
  margin-top: 100px;
  margin-bottom: 40px;
}
.footer_wrapper {
  display: flex;
  padding: 100px;
  justify-content: center;
  align-items: center;
  gap: 100px;
  flex-wrap: wrap;
  border-radius: 4px;
  background: rgba(47, 52, 57, 0.5098039216);
}
.footer_column {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  flex: 1 0 0;
  background-color: transparent;
  width: 568px;
}
.footer_column * {
  background-color: transparent;
}
.footer_title {
  margin-top: 0;
  color: #c9d7e1;
}
.footer_title span {
  color: #c70000;
}
.footer_socials {
  display: flex;
  gap: 12px;
  transition: all 0.3s ease;
}
.footer_socials a {
  text-decoration: none;
}
.footer_socials a:hover i {
  color: #c70000;
}
.footer_socials i {
  text-decoration: none;
  color: #c9d7e1;
}
.footer_contact-us {
  color: #c9d7e1;
  font-size: 24px;
  font-weight: 600;
  line-height: 125%;
}
.footer_contacts {
  display: flex;
  flex-direction: column;
  gap: 16px;
  color: #c9d7e1;
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
}
.footer_contacts i {
  width: 50px;
}
.footer_contacts a {
  color: #c9d7e1;
  flex: 1;
  align-self: flex-start;
  text-decoration: none;
  transition: 0.8s all;
}
.footer_contacts a span {
  transition: 0.8s all;
}
@media (hover: hover) {
  .footer_contacts a:hover {
    color: #c70000;
  }
  .footer_contacts a:hover svg path {
    stroke: #c70000;
  }
}

@media (max-width: 1439px) {
  .tab_content {
    max-width: 1000px;
  }
}
@media (max-width: 1199px) {
  .footer_wrapper {
    padding: 60px 40px;
  }
}
@media (max-width: 850px) {
  section {
    margin-top: 60px;
  }
}
@media (max-width: 791px) {
  .header {
    position: relative;
  }
  .header .link-text {
    font-size: 32px;
    line-height: 125%;
  }
  .header_wrapper {
    display: block;
  }
  .header_nav {
    padding-top: 60px;
    z-index: 9;
    width: 100vw;
    height: 100vh;
    position: fixed;
    right: -110%;
    font-size: 32px;
    line-height: 125%;
    transition: 0.5s all;
    background: rgba(3, 12, 16, 0.895);
  }
  .header_nav_open {
    top: 100px;
    right: 0;
    visibility: visible;
    opacity: 1;
  }
  .header_menu-link {
    z-index: 9;
    position: fixed;
    gap: 30px;
    right: -50%;
    top: 600px;
    transition: 0.5s right;
  }
  .header_menu-link .menu-link_link {
    gap: 16px;
  }
  .header_menu-link .menu-link_icon svg {
    width: 40px;
    height: 40px;
  }
  .header_menu-link_open {
    right: 50%;
    transform: translateX(50%);
    visibility: visible;
    opacity: 1;
  }
  .header_list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 60px;
  }
  .header_logo-hamburger-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header_logo-hamburger-wrapper .hamburger {
    display: flex;
    width: 44px;
    height: 44px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    gap: 8px;
    border-radius: 100px;
    border: 1px solid #455358;
  }
  .header_logo-hamburger-wrapper .hamburger span {
    display: block;
    width: 16px;
    height: 1.5px;
    background: #c2d3dc;
    transition: 0.5s all;
  }
  .header_logo-hamburger-wrapper .hamburger_open {
    z-index: 10;
  }
  .header_logo-hamburger-wrapper .hamburger_open span:nth-child(1) {
    transform: translateY(5px) rotate(-45deg);
  }
  .header_logo-hamburger-wrapper .hamburger_open span:nth-child(2) {
    transform: translateY(-5px) rotate(45deg);
  }
  .enjoy {
    padding: 100px 60px;
  }
  .gallery_img {
    max-width: 688px;
  }
  .gallery_img__rectangle {
    display: none;
  }
}
@media (max-width: 767px) {
  section {
    margin-top: 40px;
  }
  .container {
    max-width: 768px;
    padding: 0 16px;
  }
  h2 {
    font-size: 32px;
    line-height: 125%;
  }
  .enjoy {
    padding: 60px 16px;
  }
  .enjoy_title {
    font-size: 42px;
    line-height: 115%;
  }
  .about_title {
    font-size: 32px;
  }
  .gallery {
    flex-wrap: wrap;
  }
  .gallery_img img {
    height: 120%;
    object-fit: cover;
  }
  .footer_wrapper {
    padding: 40px 20px;
    gap: 20px;
  }
  .footer_column {
    min-width: 80%;
  }
  .menu_title {
    font-size: 32px;
  }
}
@media (max-width: 360px) {
  h2 {
    font-size: 24px;
  }
  .enjoy_title {
    font-size: 32px;
    text-align: center;
  }
  .enjoy_descr {
    display: none;
  }
  .enjoy_hero {
    align-items: center;
  }
  .about_title {
    font-size: 24px;
  }
  .footer_socials {
    margin: 20px auto;
  }
  .footer_column {
    gap: 0;
  }
  .footer_contact-us {
    display: none;
  }
  .footer_contacts {
    font-size: 12px;
  }
  .footer_contacts i {
    width: 30px;
  }
}